body.modal-active {
  width: 100%;
  position: fixed;
}

.modal-overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 8888;
  background-color: rgba(0, 0, 0, .5);
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: 280px;
  max-width: 95%;
  max-height: 100%;
  z-index: 9999;
  text-align: center;
  pointer-events: none;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 26px;
  transition: all .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

.modal.active, .modal-overlay.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

